.homepage{min-height:100vh;background:var(--base-palette-black-725);padding:2rem;font-family:"intervariable",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,sans-serif}.header{text-align:center;margin-bottom:3rem;color:var(--base-palette-black-0)}.header h1{font:var(--base-typography-huge);margin:0;background:var(--base-palette-gradients-green);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text;text-shadow:none}@media(max-width: 768px){.header h1{font:var(--base-typography-large)}}.subtitle{font:var(--base-typography-body_large);margin:1rem 0 0 0;opacity:.8;color:var(--base-palette-black-70)}@media(max-width: 768px){.subtitle{font:var(--base-typography-body)}}.buttonContainer{display:flex;justify-content:center;gap:1rem;margin-top:1.5rem}.actionButton{background:var(--base-palette-green-300);color:#fff;font-weight:bold;border:none;padding:.75rem 1.5rem;border-radius:var(--base-border-radius-m);cursor:pointer;transition:all .3s ease}.actionButton:hover{background:var(--base-palette-green-400);transform:scale(1.05)}.actionButton:active{transform:scale(0.95)}.loadingContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:50vh;color:var(--base-palette-black-0)}.loadingContainer p{font:var(--base-typography-body_large);margin-top:1rem;color:var(--base-palette-black-70)}.loadingSpinner{width:40px;height:40px;border:3px solid var(--base-palette-black-650);border-top:3px solid var(--base-palette-green-300);border-radius:50%;animation:spin 1s linear infinite;margin-bottom:1rem}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.scansList{max-width:1200px;margin:0 auto;display:flex;flex-direction:column;gap:2rem}@media(max-width: 768px){.scansList{gap:1.5rem;padding:0 1rem}}.emptyState{text-align:center;color:var(--base-palette-black-70);font:var(--base-typography-body_large);padding:3rem;background:var(--base-palette-black-700);border-radius:var(--base-border-radius-m);border:1px solid var(--base-palette-black-650)}.emptyState p{margin:0;opacity:.8}.modalOverlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.95);display:flex;align-items:center;justify-content:center;z-index:1000;padding:2rem;animation:fadeIn .3s ease;backdrop-filter:blur(4px)}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.modalContent{position:relative;max-width:90vw;max-height:90vh;background:var(--base-palette-black-675);border-radius:var(--base-border-radius-m);overflow:hidden;box-shadow:0 25px 50px rgba(0,0,0,.8);border:1px solid var(--base-palette-black-600);animation:slideIn .3s ease}@keyframes slideIn{from{transform:scale(0.9);opacity:0}to{transform:scale(1);opacity:1}}.modalClose{position:absolute;top:1rem;right:1rem;background:var(--base-palette-black-650);border:1px solid var(--base-palette-black-600);border-radius:50%;width:40px;height:40px;display:flex;align-items:center;justify-content:center;cursor:pointer;z-index:1001;color:var(--base-palette-black-0);transition:all .3s ease}.modalClose:hover{background:var(--base-palette-black-600);border-color:var(--base-palette-green-300);transform:scale(1.1);color:var(--base-palette-green-300)}.modalClose:active{transform:scale(0.95)}.modalImage{max-width:100%;max-height:90vh;width:auto;height:auto;display:block}.originalImageHeader{padding:1.5rem;border-bottom:1px solid var(--base-palette-black-600);background:var(--base-palette-black-700)}.originalImageHeader h3{margin:0;color:var(--base-palette-black-0);font:var(--base-typography-h4);display:flex;align-items:center;gap:.5rem}.originalImageHeader h3::before{content:"📄";font-size:1.2em}.checkTumorHeader{padding:1.5rem;border-bottom:1px solid var(--base-palette-black-600);background:var(--base-palette-black-700)}.checkTumorHeader h3{margin:0;color:var(--base-palette-black-0);font:var(--base-typography-h4);display:flex;align-items:center;gap:.5rem}.checkTumorHeader h3::before{content:"🧠";font-size:1.2em}.checkTumorForm{padding:1.5rem;display:flex;flex-direction:column;gap:1rem}.formGroup{display:flex;flex-direction:column;gap:.5rem;position:relative}.formGroup label{color:var(--base-palette-black-0);font:var(--base-typography-body);opacity:.9}.selectInput,.fileInput{background:var(--base-palette-black-700);border:1px solid var(--base-palette-black-650);color:var(--base-palette-black-0);padding:.75rem;border-radius:var(--base-border-radius-m);font:var(--base-typography-body)}.selectInput:focus,.fileInput:focus{border-color:var(--base-palette-green-300);outline:none}.submitButton{background:var(--base-palette-green-300);color:var(--base-palette-black-725);border:none;padding:.75rem;border-radius:var(--base-border-radius-m);font:var(--base-typography-body);cursor:pointer;transition:all .3s ease;margin-top:1rem}.submitButton:hover{background:var(--base-palette-green-400)}.submitButton:active{transform:scale(0.98)}.customSelect{background:var(--base-palette-black-700);border:1px solid var(--base-palette-black-650);color:var(--base-palette-black-0);padding:.75rem;border-radius:var(--base-border-radius-m);font:var(--base-typography-body);cursor:pointer;position:relative}.customSelect::after{content:"▼";position:absolute;right:1rem;top:50%;transform:translateY(-50%);color:var(--base-palette-black-0)}.optionsList{list-style:none;padding:0;margin:0;background:var(--base-palette-black-700);border:1px solid var(--base-palette-black-650);border-radius:var(--base-border-radius-m);max-height:150px;overflow-y:auto;position:absolute;top:calc(100% + .5rem);left:0;width:100%;z-index:1002}.listItem{padding:.75rem;cursor:pointer;color:var(--base-palette-black-0);transition:background .3s ease}.listItem:hover{background:var(--base-palette-black-650)}.listItem:not(:last-child){border-bottom:1px solid var(--base-palette-black-650)}